@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);
.notosanskr * {
 font-family: 'Noto Sans KR', sans-serif;
}

body{
  font-family: 'Noto Sans KR', sans-serif;
}



.App {
  text-align: left;
  margin: 0 auto;
  padding: 20px;
}
/* 헤더 */
.header{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 500px;
  padding: 10px;
  background-image: url("https://tistory1.daumcdn.net/tistory/0/pg_Poster/showcase_bg.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: rgb(11, 11, 11);
  flex-direction: column;
}
.Listbtn{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  color: rgb(11, 11, 11);
  margin-left: 55px;
  margin-top: 15px;
}

.user-nickname {
  margin-right: 10px;
}
.header-1{
  cursor: pointer;
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
}
.header-2 {
  display: flex;
  align-items: center;
}

.header span {
 
  font-size: 16px; font-weight: bold; vertical-align: middle;
}.header button {
  width: 100px;
  height: 40px;
  margin-left: 10px;
  padding: 5px 10px;
  background-color: #73728200;
  color: rgba(255, 255, 255, 0.713);
  border: 1px solid rgba(255, 255, 255, 0.581);
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s, border-color 0.3s; /* Add smooth transition */

}

.header button:hover {
  background-color: #73728269; /* Change background color on hover */
  color: white; /* Change text color on hover */
  border-color: white; /* Change border color on hover */
}

.user-info {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.user-details {
  margin-left: 10px;
}
.user-nickname {
  font-size: 16px;
  font-weight: bold;
}
.user-email {
  font-size: 14px;
  color: #ccc;
}
.nav {
  display: flex;
  justify-content: space-around;
  padding: 10px;
  background-color: #555;
  width: 100%;
  color:white;
  border-bottom: 1px solid black;
}
.nav>button{
  width:100%
}
.nav>button:hover{
  cursor: pointer;
  background-color: lightgray;
  color: white;
}
.nav a {
  color: white;
  text-decoration: none;
}
/* 공용 */
* {
  box-sizing: border-box;
}
table{
  border-collapse: collapse;
}
thead{
  background-color: darkgray;
}
th{
  border : 1px solid black;
}
td{
  border : 1px solid black;
}
button{
  background: white;
  outline: none;
  border: 0;
  cursor: pointer;
}
.ListArea{
  display: flex;
    flex-direction: column;
    align-content: center;
    flex-wrap: wrap;
}
.btnInsert,.btnList{
  margin-left: 10px;
  padding: 5px 10px;
  
  background-color: #8280a0;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

Link{
  text-decoration: none;
}
.container {
  cursor: pointer;
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 1000px;
  background-color: #fff; 
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0px 5px 10px rgba(15, 14, 14, 0.5); /* 그림자 효과 */
}

.post {
  width: 90%;
  margin-bottom: 20px;
}

.post-title {
  font-size: 1.5em;
  font-weight: bold;
  color: #333; /* 제목 글자색 */
}

.post-content {
  margin-top: 10px;
  color: #666; /* 내용 글자색 */
}
/* 게시글 상세보기 */
.detail-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}
.detail-table th, .detail-table td {
  border: 1px solid #ddd;
  padding: 8px;
}
.detail-table th {
  background-color: #F2F2F2;
}
.detail-table td[colSpan="3"] {
  height: 200px;
}
.btn-area {
  margin-top: 20px;
}
.btn-area a {
  text-decoration: none;
  background-color: #4CAF50;
  color: white;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
}
.btn-area a:hover {
  background-color: #45A049;
}

/* 모달 */
.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal-content {
  background: rgba(97, 99, 100, 0.959);
  padding: 30px;
  border-radius: 10%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  max-width: 300px;
  width: 80%;
  margin: 0 auto;
  color: #fff;
}

.close {
  color: #fff;
  position: absolute;
  top: 15px;
  right: 15px;
  font-size: 24px;
  cursor: pointer;
}
.login-form{
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
}
.login-form label {
  display: block;
  margin-bottom: 10px;
  color: #fff;
}

.login-form input {
  width: 108%;
  padding: 15px;
  margin-bottom: 20px;
  border: none;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.8);
  color: #333;
}

.login-form button {
  background:  #ab99ab;
  color: white;
  border: none;
  padding: 15px;
  border-radius: 5px;
  cursor: pointer;
  
}

.login-form button:hover {
  background: #6a5d6a;
}
