
.MainBoard {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-template-rows: repeat(3, 1fr);
    grid-gap: 5px;
    max-width: 1000px;
    margin: 0 auto;
    margin-top: 50px;
    padding: 20px;
    border-radius: 10px;
  }
  .cellText{
    cursor: pointer;
  }
  .cell {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    border: 0;
    box-sizing: border-box;
    overflow: hidden;
    transition: box-shadow 0.2s ease; /* Fade effect */
  }
  
  .cell:hover {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); /* Add shadow on hover for a three-dimensional look */
  }
  
  .image {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease; /* Fade effect for the image */
  }
  
  .cell:hover .image {
    transform: scale(1.1); /* Slightly enlarge the image on hover */
  }
  
  .cell:nth-child(odd) {
    background-color: rgba(255, 255, 255, 0.8); /* Slightly transparent white for odd cells */
  }
  .cell:nth-child(even) {
    font-size: 50px;
    text-align: center;
    color: black;
  }