.board-detail-container {
    max-width: 600px;
    margin: 50px auto;
    background-color: #95a0ad42;
    padding: 30px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    border-radius: 8px;
  }
  
  .board-detail-title {
    color: #000000;
    text-align: center;
    font-size: 24px;
    margin-bottom: 20px;
  }
  
 
  .form-btn{
    margin-left: 190px;
  }
  .label {
    display: block;
    margin-bottom: 8px;
    color: #555;
    font-weight: bold;
    font-size: 16px;
  }
  .labeldate{
    display: block;
    padding-left: 15px;
    margin-bottom: 15px;
    color: #555;
    font-weight: bold;
    font-size: 16px;
  }
  
  .input-group {
    display: flex;
    align-items: center;
    
  }
  
  .input-group label {
    flex: 0 0 70px;
    margin-right: 1px;
  }
  
  .input-group input,
  .input-group textarea {
    
    width: 100%;
    padding: 15px;
    margin-bottom: 16px;
    border: 1px solid #ddd;
    border-radius: 4px;
    box-sizing: border-box;
    font-size: 15px;
    font-weight: bold;
  }
  
  .btn-delete {
    background-color: #5d4949d5;
    color: #fff;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    transition: background-color 0.3s;
  }
  .btn-update{
    margin-right: 10px;
    background-color: #7d7d6c;
    color: #fff;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    transition: background-color 0.3s;
  }
  .btn-list{
    background-color: #51516186;
    margin-right: 10px;
    color: #fff;
    padding: 7px 11px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    display: inline-block;
    font-size: 13px;
    transition: background-color 0.3s;
  }
  .btn-list:hover{
    background-color: #373750e9;
  }
  .btn-delete:hover {
    background-color: #ac0a0a;
  }
  .btn-update:hover {
    background-color: #928601;
  }
